body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 20px 40px;
  background: rgb(18 21 29/1);
  color: rgb(255 255 255/1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

li,
p {
  color: rgb(156, 163, 175);
  line-height: 1.6;
}

p {
  padding: 0 20px;
}

.colorBlue {
  color: rgb(6, 182, 212);
  margin-block: 30px;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
}

strong {
  color: rgb(255 255 255/1);
}

img {
  width: 100%;
  height: 100%;
}

figcaption {
  color: rgb(255, 255, 255);
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 20px;
  text-align: center;
}
